div.tox-tinymce {
  border: 1px solid var(--input-border-color);
  border-radius: .25rem;

  &:hover,
  &.tox-edit-focus {
    --input-border-color: var(--input-focus-border-color);
  }
}

.tox-edit-focus .tox-edit-area__iframe {
  background-color: var(--input-focus-bg-color) !important;
}

.tox-editor-header {
  box-shadow: none !important;
  padding: 0 !important;
}

.tox .tox-toolbar__group {
  border: 0 !important;
  padding: 0.375rem !important;
}

.tox .tox-tbtn {
  margin: 0 !important;
}

.tox-editor-header {
  border-bottom: 1px solid var(--input-border-color) !important;
}

.tox-toolbar,
.tox-toolbar__overflow,
.tox-toolbar__primary {
  background: none !important;
}

.tox .tox-edit-area::before {
  border: 0 !important;
}

.form-row:has(.tox-tinymce) .label {
  padding: .5rem 0;
}